<template>
  <div>
    <van-nav-bar title="黑马头条-登录" />
    <div>
      <van-form @submit="onSubmit">
        <van-field v-model="user.mobile" name="mobile" label="手机号"
          placeholder="手机号" required
          :rules="[{ required: true, message: '请填写正确的手机号',pattern:/^1[3-9]\d{9}$/ }]" />
        <van-field v-model="user.code" type="password" name="code" label="密码"
          placeholder="密码" required
          :rules="[{ required: true, message: '请填写密码' ,pattern:/^\d{6}$/}]" />
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit"
            :disabled='isLoading' :loading='isLoading' loading-text="登录中">登录
          </van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { loginAPI } from '@/api/index'
import Notify from '@/ui/Notify.js'
import { setToken } from '@/utils/token'
import { setStorage } from '@/utils/storage'
export default {
  data () {
    return {
      user: {
        mobile: '13362758598', // 手机号
        code: '246810'// 验证码(密码-必须是246810)
      },
      isLoading: false
    }
  },
  methods: {
    async onSubmit (values) {
      // console.log('submit', values)
      this.isLoading = true
      try {
        const res = await loginAPI(values)
        console.log(res)
        Notify({ type: 'success', message: '登录成功' })
        setToken(res.data.data.token)
        setStorage('refresh_token', res.data.data.refresh_token)
        // js改变路由，this.$router.push() 压栈(会产生历史记录，可以回退)
        // this.$router.replace() 替换(不会产生历史记录)
        console.log(this.$store.state.errUrl)
        this.$router.replace({
          path: this.$route.query.path || 'layout/home' // 跳转到上一页或者首页
        })
      } catch (error) {
        // console.log(error)
        Notify({ type: 'danger', message: '账号或密码错误' })
      }
      this.isLoading = false
    }
  }
}
</script>

